<template>
	<div class="loginWrapper">
		<div class="top">
			<img src="../assets/login_bg.png" />
		</div>
		<div class="loginAccount_warpper borderCommon">
			<div class="container">
				<div class="left">
					<img class="label_icon" src="../assets/login_mobile_icon.png" />
				</div>
				<div class="right">
					<input type="tel" placeholder="请输入手机号" maxlength="11" v-model="telNum" />
				</div>
			</div>
		</div>
		<div class="msgCode_wrapper">
			<div class="container">
				<div class="left_wrapper borderCommon">
					<div class="left">
						<img class="label_icon" src="../assets/login_password_icon.png" />
					</div>
					<div class="right">
						<input type="tel" placeholder="短信验证码" maxlength="6" v-model="msgNum" />
					</div>
				</div>
				<div class="right_wrapper borderCommon text_center getMsg_btn" @click="getMsgCodeFn($event)">获取验证码</div>
				<div style="clear:both"></div>
			</div>
		</div>
		<div class="login_btn text_center" @click="loginFn">登录</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';

	export default {
		name: 'loginWrapper',
		data() {
			return {
				telNum: '', // 手机号码
				msgNum: '', // 验证码
			}
		},
		methods: {
			// 获取验证码点击事件
			getMsgCodeFn(e) {
				let getBtn = e.target;
				let num = 60;

				// 验证手机号是否输入
				if(this.telNum == '') {
					Toast({
						message: '手机号不能为空',
						position: 'middle',
						duration: 5000
					})
				} else {
					// 手机号正则验证
					var telReg = /^1(3|4|5|7|8)\d{9}$/.test(this.telNum);

					if(telReg) {
						let timer = setInterval(countDown, 1000);

						function countDown() { // 获取短信验证码倒计时函数
							num--;
							console.log(num)
							getBtn.innerHTML = num + "s后重发";
							getBtn.classList.add('getMsg_btn-active');
							if(num == 0) {
								getBtn.innerHTML = "获取验证码";
								getBtn.classList.remove('getMsg_btn-active');
								clearInterval(timer);
							}
						}
					} else {
						Toast({
							message: '请填写正确的手机号',
							position: 'middle',
							duration: 5000
						})
					}
				}
			},
			// 登录按钮点击事件
			loginFn() {
				// 手机号正则验证
				var telReg = /^1(3|4|5|7|8)\d{9}$/.test(this.telNum);

				// 验证手机号是否输入
				if(this.telNum == '') {
					Toast({
						message: '手机号不能为空',
						position: 'middle',
						duration: 5000
					})
				} else {
					if(telReg) {
						if(this.msgNum == '') {
							Toast({
								message: '验证码不能为空',
								position: 'middle',
								duration: 5000
							})
						} else {
							this.$http.post(process.env.BASE_API + '/accountController/Zcblogin', {
									userId: '7629',
									mobile: '17891952215',
									code: '12355',
								})
								.then(function(data) {
									console.log(data);
									// this.$router.push('/index')
								})
								.catch(function(error) {

								})
						}
					} else {
						Toast({
							message: '请填写正确的手机号',
							position: 'middle',
							duration: 5000
						})
					}
				}
			},
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.loginWrapper .top {
		width: 93%;
		margin: 0 auto 25px;
	}
	
	.loginWrapper .top img {
		width: 100%;
	}
	
	.loginWrapper .loginAccount_warpper {
		width: 80%;
		height: 42px;
		margin: 0 auto 20px;
	}
	
	.container {
		width: 100%;
		height: 100%;
	}
	
	.loginWrapper .loginAccount_warpper .container .left {
		width: 18.83%;
		height: 100%;
		float: left;
		position: relative;
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		align-items: center;
	}
	
	.loginWrapper .label_icon {
		width: 21px;
	}
	
	.loginWrapper .loginAccount_warpper .container .left::after {
		content: '';
		display: inline-block;
		width: 1px;
		height: 25px;
		background: #ccc;
		position: absolute;
		top: 9px;
		right: 0;
	}
	
	.loginWrapper .loginAccount_warpper .container .right {
		width: 81%;
		height: 100%;
		float: right;
	}
	
	.loginWrapper .msgCode_wrapper {
		width: 80%;
		border-radius: 4px;
		margin: 0 auto 25px;
	}
	
	.loginWrapper .msgCode_wrapper .container .left_wrapper {
		width: 58.3%;
		height: 40px;
		float: left;
	}
	
	.loginWrapper .msgCode_wrapper .container .left_wrapper .left {
		width: 32.28%;
		height: 40px;
		float: left;
		position: relative;
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		align-items: center;
	}
	
	.loginWrapper .msgCode_wrapper .container .left_wrapper .left::after {
		content: '';
		display: inline-block;
		width: 1px;
		height: 25px;
		background: #ccc;
		position: absolute;
		top: 9px;
		right: 0;
	}
	
	.loginWrapper .msgCode_wrapper .container .left_wrapper .right {
		width: 67.72%;
		height: 40px;
		float: right;
	}
	
	.loginWrapper .msgCode_wrapper .container .right_wrapper {
		width: 38.3%;
		height: 40px;
		line-height: 40px;
		float: right;
		font-size: 15px;
		color: #4A77B0;
		cursor:pointer;
	}
	
	.loginWrapper .login_btn {
		width: 80%;
		height: 44px;
		line-height: 44px;
		background: #4A77B0;
		color: #fff;
		font-size: 17px;
		margin: 0 auto;
		border-radius: 4px;
		cursor:pointer;
	}
	
	.getMsg_btn-active {
		cursor: not-allowed;
		background-color: #f5f5f5;
		color: #d9d9d9!important;
	}
	
	.borderCommon {
		border: 1px solid #ccc;
		border-radius: 4px;
	}
	
	input {
		width: 99%;
	}
</style>


