	<div class="robBillWrapper">
		<!-- modal组件 -->
		<modal-mod :headTxt="headerMsg" :sectionTxt="sectionMsg" :ensureBtnTxt="ensureMsg" :cancelBtnTxt="cancelMsg" ref="modalChild"></modal-mod>
		<!-- 搜索框组件 -->
		<search-mod></search-mod>
		<div class="tab_wrapper">
			<div class="tab_item" :class="{tab_active : tabActiveIndex == index}" v-for='(item,index) in tabLists' @click="tabChangeFn(index)">{{item}}</div>
		</div>
		<div class="tab_container">
			<!-- 列表数据统计 -->
			<div class="list_count">共<span class="num">{{listCount}}</span>条数据</div>
			<!-- 可抢单tab -->
			<div class="allowRobBill_tab tabItem_cnt" v-show="tabActiveIndex == 0">
				<ul class="listBox">
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn" @click="cancelModal">取消</div>
							<div class="robBill_btn btn" @click="ensureModal">抢单剩余(999s)</div>
						</div>
					</li>
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn">取消</div>
							<div class="robBill_btn btn">抢单剩余(999s)</div>
						</div>
					</li>
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn">取消</div>
							<div class="robBill_btn btn">抢单剩余(999s)</div>
						</div>
					</li>
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn">取消</div>
							<div class="robBill_btn btn">抢单剩余(999s)</div>
						</div>
					</li>
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn">取消</div>
							<div class="robBill_btn btn">抢单剩余(999s)</div>
						</div>
					</li>
					<li>
						<div class="top">
							<p class="name text_center">提浩菲</p>
							<p class="detail_info">
								<span class="gender">男</span>&nbsp;
								<span class="age">32</span>&nbsp;
								<span class="degree">本科</span>
							</p>
							<p class="job text_center">苏州华硕-操作工</p>
						</div>
						<div class="bottom">
							<div class="cancel_btn btn">取消</div>
							<div class="robBill_btn btn">抢单剩余(999s)</div>
						</div>
					</li>
				</ul>
			</div>
			<!-- 已超时tab -->
			<div class="allowRobBill_tab tabItem_cnt" v-show="tabActiveIndex == 1">
				已超时页面
			</div>
		</div>
		<footer-btn></footer-btn>
	</div>
</template>

<script>
	import search from './search.vue'
	import modal from './modal.vue'

	export default {
		name: 'robBillWrapper',
		data() {
			return {
				tabLists: ['可抢单', '已超时'], // tab数据数组
				tabActiveIndex: 0, // 默认可抢单tab被点亮
				listCount: 30, // 列表数据统计
				// modal组件父传子数据
				headerMsg: '', // modal顶部标题
				sectionMsg: '', // modal内容
				ensureMsg: '', // modal确定按钮文本
				cancelMsg: '', // modal取消按钮文本
			}
		},
		methods: {
			// tab切换
			tabChangeFn(index) {
				this.tabActiveIndex = index;
			},
			// 取消抢单按钮点击事件
			cancelModal() {
				this.$refs.modalChild.isHide = false; // 显示modal
				this.$refs.modalChild.isGray = true; // 按钮显示灰色边框
				this.$refs.modalChild.isBlue = true; // 按钮显示蓝色背景
				this.headerMsg = "";
				this.sectionMsg = '取消后，将无法查看此订单是否确认取消订单？';
				this.ensureMsg = '确定取消';
				this.cancelMsg = '暂不取消';
			},
			// 确定抢单按钮点击事件
			ensureModal() {
				this.$refs.modalChild.isHide = false; // 显示modal
				this.$refs.modalChild.isRed = true; // 显示红色字体
				this.headerMsg = "抢单成功";
				this.sectionMsg = '在“跟单-等待沟通”模块查看已抢单数据';
				this.ensureMsg = '继续抢单';
				this.cancelMsg = '去处理';
			},
		},
		components: {
			'search-mod': search,
			'modal-mod': modal,
		}
	}
</script>

<style scoped>
	.robBillWrapper .tab_wrapper {
		width: 100%;
		height: 36px;
		display: flex;
		display: -webkit-flex;
		position: fixed;
		top: 55px;
		left: 0;
		background: #fff;
		z-index: 99;
	}
	
	.robBillWrapper .tab_wrapper .tab_item {
		color: #666;
		font-size: 14px;
		flex: auto;
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
	}
	
	.tab_active {
		font-weight: 600;
		color: #4a77b0!important;
		position: relative;
	}
	
	.tab_active::after {
		content: '';
		display: inline-block;
		width: 50%;
		height: 2px;
		background: #4a77b0;
		position: absolute;
		top: 36px;
		left: 26%;
	}
	
	.robBillWrapper .tab_container {
		padding: 91px 0 49px;
		box-sizing: border-box;
	}
	
	.robBillWrapper .tab_container .list_count {
		width: 100%;
		height: 31px;
		line-height: 31px;
		background: #f5f5f5;
		padding: 0 20px;
		box-sizing: border-box;
		font-size: 14px;
		color: #666;
	}
	
	.robBillWrapper .tab_container .list_count .num {
		color: #4A77B0;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt {
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;
		background: #f5f5f5;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li {
		width: 100%;
		height: 180px;
		background: #fff;
		border-radius: 4px;
		padding: 0 20px;
		box-sizing: border-box;
		margin-bottom: 12px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li:last-child {
		margin-bottom: 0;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top {
		width: 100%;
		height: 114px;
		border-bottom: 1px solid #e5e5e5;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .name {
		font-size: 18px;
		color: #333;
		height: 33px;
		line-height: 43px;
		margin-bottom: 5px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .detail_info {
		color: #666;
		font-size: 14px;
		text-align: center;
		margin-bottom: 15px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .detail_info .gender,
	.age,
	.job {
		position: relative;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .detail_info .gender::after {
		content: '';
		display: inline-block;
		width: 1px;
		height: 13px;
		background: #666;
		position: absolute;
		top: 3px;
		left: 18px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .detail_info .age::after {
		content: '';
		display: inline-block;
		width: 1px;
		height: 13px;
		background: #666;
		position: absolute;
		top: 3px;
		left: 21px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .top .job {
		font-size: 16px;
		color: #333;
		margin-bottom: 20px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .bottom {
		width: 100%;
		height: 65px;
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		align-items: center;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .bottom .cancel_btn {
		width: 27.11%;
		border: 1px solid #999;
		color: #999;
		margin-right: 25px;
	}
	
	.robBillWrapper .tab_container .tabItem_cnt .listBox li .bottom .robBill_btn {
		width: 47.37%;
		color: #fff;
		background: #FD8F4D;
		border: 1px solid #FD8F4D;
	}
	
	.btn {
		text-align: center;
		height: 33px;
		line-height: 35px;
		border-radius: 4px;
		font-size: 15px;
		cursor: pointer;
	}
</style>


