<template>
	<div id="app">
		<router-view/>
	</div>
</template>

<script>
	export default {
		name: 'App',
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	ul,li,ol{
		list-style:none;
	}
	
	a{
		text-decoration: none;
	}
	
	.text_center{
		text-align: center;
	}
	
	html,body{
		width:100%;
		height:100%;
	}
	
	#app{
		width:100%;
		height:100%;
	}
	
	input{
		border: 0;
		outline: none;
		padding-left: 10px;
		box-sizing: border-box;
		font-size: 14px;
		color: #999;
		height: 100%;
	}
	
	input::-webkit-input-placeholder {
		/*WebKit browsers*/
		color: #999;
	}
	
	input::-moz-input-placeholder {
		/*Mozilla Firefox*/
		color: #999;
	}
	
	input::-ms-input-placeholder {
		/*Internet Explorer*/
		color: #999;
	}
	
</style>


