<template>
	<div class="searchWrapper">
		<div class="cnt">
			<input type="text" placeholder="求职者姓名／职位／企业" />
			<span class="searchImg_wrapper">
				<img src="../assets/searchPicture.png" />
			</span>
		</div>
	</div>
</template>

<script>
	export default{
		name:'searchWrapper',
		data(){
			return{
				
			}
		},
		methods:{
			
		}
	}
</script>

<style scoped>
	.searchWrapper{
		width:100%;
		height:55px;
		background:#4a77b0;
		padding:0 20px;
		box-sizing: border-box;
		display:flex;
		display:-webkit-flex;
		align-items: center;
		position:fixed;
		top:0;
		left:0;
		z-index:99;
	}
	
	.searchWrapper .cnt{
		width:100%;
		height:34px;
		background:#fff;
		border-radius:4px;
		position:relative;
	}
	
	.searchWrapper .cnt::after{
		content:'';
		display:inline-block;
		width:1px;
		height:20px;
		background:#C9C9C9;
		position:absolute;
		top:7px;
		left:87%;
	}
	
	.searchWrapper .cnt .searchImg_wrapper{
		display:inline-block;
		width:13%;
		height:100%;
		float:left;
		border-top-right-radius:4px;
		border-bottom-right-radius:4px;
		display:flex;
		display:-webkit-flex;
		justify-content: center;
		align-items: center;
		cursor:pointer;
	}
	
	input{
		width:87%;
		float:left;
		border-top-left-radius:4px;
		border-bottom-left-radius:4px;
	}
	
	img{
		width:16px;
	}

</style>


