<template>
	<div class="footerWrapper">
		<div class="btn_item" @click="switchTo('/robbill')">
			<img :src="'/robbill' === $route.path ? tabBarImgArr[0].selected : tabBarImgArr[0].normal" alt="抢单">
			<span :class="{active: '/robbill' === $route.path}">抢单</span>
		</div>
		<div class="btn_item" @click="switchTo('/followbill')">
			<img :src="'/followbill' === $route.path ? tabBarImgArr[1].selected : tabBarImgArr[1].normal" alt="跟单">
			<span :class="{active: '/followbill' === $route.path}">跟单</span>
		</div>
		<div class="btn_item">
			<img id="plusImg" src="../assets/plus_img.png" alt="">
		</div>
		<div class="btn_item" @click="switchTo('/position')">
			<img :src="'/position' === $route.path ? tabBarImgArr[2].selected : tabBarImgArr[2].normal" alt="职位">
			<span :class="{active: '/position' === $route.path}">职位</span>
		</div>
		<div class="btn_item" @click="switchTo('/my')">
			<img :src="'/my' === $route.path ? tabBarImgArr[3].selected : tabBarImgArr[3].normal" alt="我的">
			<span :class="{active: '/my' === $route.path}">我的</span>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'footerWrapper',
		data() {
			return {
				// 底部按钮切换
				tabBarImgArr: [{
						normal: require('../assets/grabSheet.png'),
						selected: require('../assets/grabSheet_checked.png')
					},
					{
						normal: require('../assets/documentary.png'),
						selected: require('../assets/documentary_checked.png')
					},
					{
						normal: require('../assets/recruiting.png'),
						selected: require('../assets/recruiting_checked.png')
					},
					{
						normal: require('../assets/user.png'),
						selected: require('../assets/user_checked.png')
					}
				]
			}
		},
		methods: {
			switchTo(path) {
				this.$router.replace(path);
			}
		}
	}
</script>

<style scoped>
	.footerWrapper {
		width: 100%;
		height: 49px;
		background: #fff;
		position: fixed;
		left: 0;
		bottom: 0;
		display: flex;
		display: -webkit-flex;
		justify-content: space-around;
		align-items: center;
		padding-top:5px;
		box-sizing: border-box;
	}
	
	.footerWrapper .btn_item {
		height: 38.5px;
		display: flex;
		display: -webkit-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		cursor: pointer;
	}
	
	.footerWrapper .btn_item img {
		height: 22px;
	}
	
	#plusImg{
		height:35px;
	}
	
	.footerWrapper .btn_item span {
		font-size: 14px;
		color: #666;
	}
	
	.active {
		color: #4A77B0!important;
	}
</style>


