<template>
	<div class="modalWrapper">
		<!-- mask -->
		<div class="mask" @click="maskClickFn" :class="{hide: isHide}"></div>
		<!-- modal -->
		<div class="modal" :class="{hide: isHide}">
			<div class="modal-header text_center" :class="{red: isRed}">{{headTxt}}</div>
			<div class="modal-section text_center">{{sectionTxt}}</div>
			<div class="modal-footer">
				<div class="ensure_btn btn" style="margin-right:11.67%;" :class="{grayBorder: isGray}">{{ensureBtnTxt}}</div>
				<div class="cancel_btn btn" :class="{cancelBtnStyle: isBlue}">{{cancelBtnTxt}}</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'modalWrapper',
		props:['headTxt','sectionTxt','ensureBtnTxt','cancelBtnTxt'],
		data(){
			return{
				isHide: true, // mask默认不显示
				isRed:false, // 红色文本
				isGray: false, // 底部按钮灰色边框
				isBlue: false, // 底部按钮蓝色背景
			}
		},
		methods:{
			// mask点击事件
			maskClickFn(){
				this.isHide = true; // 隐藏modal
			}
		}
	}
</script>

<style scoped>
	.mask{
		position:fixed;
		top:0;
		left:0;
		width:100%;
		height:100%;
		background:rgba(0,0,0,.5);
		z-index:999;
	}
	
	.modal{
		width:80%;
		background:#fff;
		border-radius:4px;
		position:fixed;
		left:50%;
		top:50%;
		margin-left:-40%;
		margin-top:-85px;
		z-index:1000;
		padding:25px 20px;
		box-sizing: border-box;
	}
	
	.modal .modal-header{
		font-size:18px;
		margin-bottom:14px;
	}
	
	.modal .modal-section{
		font-size:14px;
		color:#666;
		margin-bottom:25px;
	}
	
	.modal .modal-footer{
		display:flex;
		display:-webkit-flex;
		justify-content: center;
		align-items: center;
	}
	
	.modal .modal-footer .btn{
		width:30%;
		height:35px;
		line-height:35px;
		text-align: center;
		font-size:15px;
		color:#4A77B0;
		border:1px solid #4A77B0;
		border-radius:4px; 
		cursor:pointer;
	}
	
	.red{
		color:#E6453B!important;
	}
	
	.grayBorder{
		border-color:#999!important;
		color:#999!important;
	}
	
	.cancelBtnStyle{
		background:#4A77B0;
		color:#fff!important;
	}
	
	.hide{
		display:none;
	}
</style>


