import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import robBill from "@/components/robBill"
import followBill from "@/components/followBill"
import positionPage from "@/components/position"
import myPage from "@/components/my"



Vue.use(Router)

export default new Router({
//	mode: 'history',
	routes: [{
			path: '/login',
			name: 'login',
			component: login,
		},
		{
			path: '/robbill',
			name: 'robBillWrapper',
			component: robBill,
		},
		{
			path:'/followbill',
			name:'followBillWrapper',
			component:followBill,
		},
		{
			path:'/position',
			name:'positionWrapper',
			component:positionPage,
		},
		{
			path:'/my',
			name:'myPageWrapper',
			component:myPage,
		},	
		{
			path: '/',
			redirect: '/robbill'
		}
	]
})


